class HashTable {
  constructor(size = 53) {
    this.keyMap = new Array(size);
  }

  _hash(key) {
    let total = 0;
    let WEIRD_PRIME = 31;
    for (let i = 0; i < Math.min(key.length, 100); i++) {
      let char = key[i];
      let value = char.charCodeAt(0) - 96
      total = (total * WEIRD_PRIME + value) % this.keyMap.length;
    }
    return total;
  }

  _set(key, value) {
    const index = this._hash(key); //get a hash from the key
    if (!this.keyMap[index]) { //if there is no key value pair at hash location, instantiate new array at that location
      this.keyMap[index] = [];
    }
    this.keyMap[index].push([ key, value ]);  //push data to hash location
  }

  _get(key) {
    const index = this._hash(key); //get a hash from the key
    // if (this.keyMap[index]) {
    //   this.keyMap[index].forEach(keyValuePair => { //search for each keyValuePair, if key in keyValuePair matches input key, return value;
    //     if (keyValuePair[0] === key) {
    //       return keyValuePair[1];
    //     }
    //   });
    for (let i = 0; i < this.keyMap[index].length; i++) {
      if (this.keyMap[index][i][0] === key) {
        return this.keyMap[index][i][1];
      }
    }
    return undefined; //if key is in not in the keyValuePairs, return undefined
  }

  _keys() {
    const keys = [];
    let tableIndex = 0;
    let bucketIndex = 0;
    const currentBucket = this.keyMap[tableIndex];
    
    while (tableIndex < this.keyMap.length) {
      if (currentBucket) {
        const key = currentBucket[bucketIndex][0];
        keys.push(key);

        if (bucketIndex < currentBucket.length) {
          bucketIndex += 1;
        } 
      } else {
        bucketIndex = 0;
        tableIndex += 1;
      }
    }
    
    return keys;
  }

  _values() {
    const values = [];
    let tableIndex = 0;
    let bucketIndex = 0;
    const currentBucket = this.keyMap[tableIndex];
    
    while (tableIndex < this.keyMap.length) {
      if (currentBucket) {
        const value = currentBucket[bucketIndex][0];
        values.push(value);

        if (bucketIndex < currentBucket.length) {
          bucketIndex += 1;
        } 
      } else {
        bucketIndex = 0;
        tableIndex += 1;
      }
    }
    
    return values;
  }


 

}



const hashtable = new HashTable();
hashtable._set('fart', 44);
console.log(hashtable._keys());